import { HttpResponse } from '@angular/common/http';
import { Directive, ElementRef, EventEmitter, Input, Output } from '@angular/core';
import { _HttpClient } from '@delon/theme';
import { saveAs } from 'file-saver';

@Directive({
  selector: '[down-file]',
  exportAs: 'downFile',
  host: {
    '(click)': '_click()',
  },
})
export class DownFileDirective {
  /** URLè¯·æ±åæ° */
  @Input('http-data') httpData: {};
  /** è¯·æ±ç±»å */
  @Input('http-method') httpMethod: string = 'get';
  /** ä¸è½½å°å */
  @Input('http-url') httpUrl: string;
  /** æå®æä»¶åï¼è¥ä¸ºç©ºä»æå¡ç«¯è¿åç `header` ä¸­è·å `filename`ã`x-filename` */
  @Input('file-name') fileName: string;
  /** æååè° */
  @Output() readonly success = new EventEmitter<HttpResponse<Blob>>();
  /** éè¯¯åè° */
  @Output() readonly error = new EventEmitter<{}>();

  private getDisposition(data: string | null) {
    const arr: Array<{}> = (data || '')
      .split(';')
      .filter(i => i.includes('='))
      .map(v => {
        const strArr = v.split('=');
        const utfId = `UTF-8''`;
        let value = strArr[1];
        if (value.startsWith(utfId)) value = value.substr(utfId.length);
        return { [strArr[0].trim()]: value };
      });
    return arr.reduce((o, item) => item, {});
  }

  constructor(private el: ElementRef, private _http: _HttpClient) {}

  _click() {
    this.el.nativeElement.disabled = true;
    this._http
      .request(this.httpMethod, this.httpUrl, {
        params: this.httpData || {},
        responseType: 'blob',
        observe: 'response',
      })
      .subscribe(
        (res: HttpResponse<Blob>) => {
          if (res.status !== 200 || res.body!.size <= 0) {
            this.error.emit(res);
            return;
          }
          const disposition = this.getDisposition(res.headers.get('content-disposition'));
          const fileName =
            this.fileName ||
            disposition[`filename*`] ||
            disposition[`filename`] ||
            res.headers.get('filename') ||
            res.headers.get('x-filename');
          saveAs(res.body, decodeURI(fileName));
          this.success.emit(res);
          this.el.nativeElement.disabled = false;
        },
        err => {
          this.error.emit(err);
          this.el.nativeElement.disabled = false;
        },
      );
  }
}
